.heroName {
    letter-spacing: -.3rem;
    font-size: 9cqw;
    bottom: auto;
    background: transparent;
    font-family: "Inter", sans-serif;
}

.herocolor {
    z-index: 3;
}

.heropic {
    bottom: 2rem;
    filter: brightness(88%);
    mix-blend-mode: lighten;
    opacity: 1;
}

.herobg {
    border-radius: 30px 30px 0 0;
    mix-blend-mode: lighten;
    opacity: 0.2;
}